
import { reactive, toRefs, watch } from "vue";
export default {
    props: {
        commentObj: {
            type: Object,
            default: () => ({
                hotComments: [],
                comments: [],
            }),
        },
        showAll: {
            type: Boolean,
            default: false,
        },
    },
    setup(props) {
        const state = reactive({
            comments: [],
            hotComments: [],
        });
        watch(
            () => props.commentObj,
            newV => {
                state.hotComments = props.commentObj.hotComments;
                state.comments = props.showAll ? props.commentObj.comments : props.commentObj?.comments?.slice(0, 10);
            },
            { immediate: true }
        );
        return {
            ...toRefs(state),
        };
    },
};
