
import { defineComponent, reactive, toRefs } from "vue";
import { getEventCommentApi } from "@/api/common";
export default defineComponent({
    props: {
        threadId: {
            type: String,
            default: "",
        },
        showAll: {
            type: Boolean,
            default: false,
        },
    },
    setup(props) {
        const state = reactive({
            commentObj: {},
        });
        const getComments = () => {
            getEventCommentApi(props.threadId).then(res => {
                state.commentObj = res;
            });
        };
        getComments();
        return {
            ...toRefs(state),
        };
    },
});
